import React from 'react';

const tagContext = React.createContext({
    tagColorMap: {},
    //originColorSet: ['#142850', '#06623b', '#862a5c', '#0779e4', '#6b5b95',
    //                 '#4040a1', '#1d4c17', '#b2ad7f', '#d64161', '#ff9e00']
    originColorSet: ['#24292e']
});

export interface TagsProps {
  node: any;
}

const Tags = (props: TagsProps) => {
  const { node } = props;
  const { excerpt, fields, frontmatter } = node;
  const { slug } = fields;
  const { date, title, tags } = frontmatter;

  const getTagColor = function() {
    let {tagColorMap, originColorSet} = React.useContext(tagContext);
    let colorSet = [];

    return function(tag) {
      if (tagColorMap[tag]) return tagColorMap[tag];
      if (colorSet.length === 0) colorSet = JSON.parse(JSON.stringify(originColorSet));

      let random = Math.floor(Math.random() * colorSet.length);
      let randomColor = colorSet[random];
      colorSet.splice(random, 1);
      tagColorMap[tag] = randomColor;

      return randomColor;
    };
  }();

  const mapTag = tags.map((tag: string) => {
    if (tag === 'undefined') return;
    return (
      <li key={`${slug}-${tag}`} className="tag">
        <span>
          <a href={`/tags#${tag}`} style={{ color: getTagColor(tag) }}>{`#${tag}`}</a>
        </span>
      </li>
    );
  });

  return (
    <ul className="tag-list">
      {mapTag}
    </ul>
  );
}

export default Tags;
