import * as React from 'react';
import { memo, useEffect, useState } from 'react';
import { Link } from 'gatsby';
import { throttle } from 'lodash';
import Tags from '../Tags';
import Header from '../Header';
 
import './postList.scss';

export interface PostListProps {
  posts: any[];
}

const PostList = memo((props: PostListProps) => {
  const isBrowser = typeof window !== 'undefined';
  const { posts } = props;
  const storedCnt = isBrowser ? localStorage.getItem('showCnt') : 10;
  const [showCnt, setShowCnt] = useState(storedCnt ? storedCnt : 10);

  const throttleScrollHandler = throttle(() => {
    if (
      window.outerHeight > (document.querySelector('.post-list') as HTMLDivElement).getBoundingClientRect().bottom
    ) {
      setShowCnt((prev: number) => {
        if (prev >= posts.length) return prev;
        const nextCnt = prev + 10;
        return nextCnt;
      });
    }
  }, 250)

  const throttleRemovePosition = throttle(() => {
    if (isBrowser) {
      localStorage.removeItem('showCnt');
      localStorage.removeItem('scroll');
    }
  }, 250);

  useEffect(() => {
    window.addEventListener('scroll', throttleScrollHandler);
    return () => {
      window.removeEventListener('scroll', throttleScrollHandler);
    };
  }, [props]);

  useEffect(() => {
    if (isBrowser) {
      if (localStorage.getItem('showCnt') && localStorage.getItem('scroll')) {
        window.scrollTo(0, localStorage.getItem('scroll'));
      }

      window.addEventListener('scroll', throttleRemovePosition);
      return () => {
        window.removeEventListener('scroll', throttleRemovePosition);
      }; 
    }
  });

  posts.sort((a: any, b: any) => {
    const aDate = new Date(a.node.frontmatter.update ?? a.node.frontmatter.date);
    const bDate = new Date(b.node.frontmatter.update ?? b.node.frontmatter.date);

    if (aDate < bDate) return 1;
    if (aDate > bDate) return -1;
    return 0;
  });

  const filterSnippet = (() => {
    const filterWord = ['&#xA0;'];

    return (snippet) => {
      filterWord.forEach((word) => {
        if (snippet) snippet = snippet.replace(new RegExp(word, 'g'), '');
      });

      return snippet;
    };
  })();

  const setPosition = (() => {
    if (isBrowser) {
      const doc = document.documentElement;
      localStorage.setItem('showCnt', showCnt);
      localStorage.setItem('scroll', (window.pageYOffset || doc.scrollTop)  - (doc.clientTop || 0));
    }
  });

  const mapPost = posts.map((post: any, i: number) => {
    const { node } = post;
    const { excerpt, snippet, fields, frontmatter } = node;
    const { slug } = fields;
    const { date, title, tags } = frontmatter;
    const filterdSnippet = filterSnippet(snippet);
    let update = frontmatter.update;
    if (Number(update.split(',')[1]) === 1) update = null;

    return (
      <li key={slug} className={`post ${i < showCnt ? 'show' : 'hide'}`}>
        <article>
          <h2 className="title">
            <Link to={slug} onClick={setPosition}>{title}</Link>
          </h2>
          <div className="info">
            <div className="date-wrap">
              <span className="date">{date}</span>
            </div>
            {tags.length && tags[0] !== 'undefined' ? <span className="info-dot">·</span> : null}
            <Tags node={node}/>
          </div>
          <span className="excerpt">
            <Link to={slug} onClick={setPosition} dangerouslySetInnerHTML={{ __html: filterdSnippet }} />
          </span>
        </article>
      </li>
    );
  });
  return (
    <div className="post-list">
      <ul>{mapPost}</ul>
    </div>
  );
});

export default PostList;
